html {}

body {
    font-family: 'Poppins';
    background-color: #FFFFFF;
}

a {
    text-decoration: none;
}

ul {
    list-style: none;
    padding: 0;
}

article,
.wrapper {
    padding-left: 13px;
    padding-right: 13px;
}

@media (min-width:1114px) {
    article {
        max-width: 1114px;
        margin: auto;
    }
}