    @import "./variables";
    header>div {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: baseline;
    }
    
    [type="checkbox"] {
        display: none;
    }
    
    .icon__close {
        position: absolute;
        cursor: pointer;
        right: 21px;
        top: 21px;
        background: #ededed;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        line-height: 20px;
        text-align: center;
        border: 1px solid #999;
        font-size: 20px;
    }
    
    .navigation {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: #181717;
        z-index: 3;
        box-shadow: 0 0 50px rgba(0, 0, 0, .5);
        transform: translateX(-110%);
        transition: transform 1s;
        box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);
        ul {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding-left: 30%;
            li {
                margin-top: 40px;
                a {
                    font-family: 'Poppins Medium';
                    font-size: 24px;
                    line-height: 36px;
                    color: #ffffff;
                }
            }
        }
    }
    
    .icon__menu {
        cursor: pointer;
    }
    
    input[type="checkbox"]:checked~div>nav {
        transform: translateX(0);
        z-index: 2;
        top: 0;
        left: 0;
    }
    
    @media (min-width: 1114px) {
        .navigation {
            z-index: 3;
            width: auto;
            height: auto;
            position: relative;
            padding: 0 0;
            background: none;
            box-shadow: none;
            transform: none;
            transition: none;
            ul {
                flex-direction: row;
                padding: 0;
                li {
                    margin-left: 52px;
                    padding: 0;
                    a {
                        color: #333333;
                    }
                }
            }
        }
        .icon__menu {
            display: none;
        }
        .icon__close {
            display: none;
        }
        .wrapper {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: baseline;
            margin: auto;
            max-width: 1114px;
        }
    }